import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, ObjectKeys, hasWritePrivilege } from "../../../stores/Utils";

import {SearchInput_Cmpt, SvgIcon_Cmpt, CheckBox_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./styles.css"
import {  getall_collects, search_operations, SumDocuments } from "../services/server_fetching";
import {   CollectManual_Cmpt, CollectItem_Cmpt, CollectMultiManual_Cmpt } from "..";
import { getall_subaccount, get_balances_by_type } from "../../CuentasContables/services/server_fetching";
import { PickCustomer_Cmpt } from "../../ComprobanteParametrizados";
import WithScroll from "../../../components/scroll-decorator";


const SearchInput = SearchInput_Cmpt();

const ComprobanteItem = CollectItem_Cmpt();

const CobroManual = CollectManual_Cmpt();

const Svg = SvgIcon_Cmpt();

const Acc2Collect = "Acc2Collect"


const DropInput = PickCustomer_Cmpt();

const CheckBox = CheckBox_Cmpt();




const CollectList  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [loading, set_loading] = useState(false);

	const [multiProv, set_multiProv] = useState(false);



	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			updStore("list_show_type_collect","pending")
			get_balances_by_type("global_accounts","global_account_params",cb_result);
			set_obs(gen16CodeId());
			getall_subaccount();
		}
	});


	const openUpdateView =  () => {
		
	}


	



	

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search =  (v) => {
		set_loading(true)
		search_operations(cb_result)
	}


	const handle_getall=  (v) => {
		getall_collects(getStore(Acc2Collect),cb_result)
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	

	

	return (

		<div className="">
			
			<div className="accounts_header _dsplFlx" >
				<div>
					<h2>Cobros</h2>
				</div>
				<div className="flexSpace"/>
				
			</div>

		
			<div className="" style={{maxWidth: "720px", padding: 16}}>
			
			<div className={""} style={{height:190}}>

				<MenuAcc handle_getall={handle_getall} data={data} cb_result={cb_result} updObs={set_obs}/>
			</div>

			
			
			<div className="accounts_container scollVh scoll_CP_Height">
				{getStore(Acc2Collect)?.account && getStore("collect_by_doc") && ObjectKeys(getStore("collect_by_doc"))?.map(cid=>{
					let docAmount = getStore("collect_by_doc")[cid].amount*1;
					if((getStore("list_show_type_collect")==="done" && docAmount === 0) || (getStore("list_show_type_collect")==="pending" && docAmount !== 0)){
						return (
							<ComprobanteItem 
								data={getStore("collect_by_doc")[cid]} 
								elmId={cid} 
								key={cid}
							/>
						)
					}
					else{
						return null;
					}
				})}
			</div>
			</div>
		</div>
	);
};

export default CollectList;






const MenuAcc = ({handle_getall, data, cb_result, updObs}) =>{

	const [obs, set_obs] = useState(0);
	const [float, setFloat] = useState(false);

	const openAddView =  () => {
		
		let dataModal = {};
		dataModal["zIndex"] = 450;
		dataModal["height"] = "93vh";
		// data['observeResize']=true;
		dataModal["props"] = { minHeight: "1vh", overlay: true };
		dataModal["content"] = <CobroManual confirm={handle_getall} item={data} />
		OpenModal(dataModal);
	}




	const handleScroll = (e) => {
		
		if(e>130){
			setFloat(true);
		}else{
			setFloat(false)
		}
	}



	
	const handleSearchAccount = (v) => {
		let nn= {
			"account":v?.account,
			"subAccount":v?.subAccount,
			customerId:v?.customerId
		};
		updStore(Acc2Collect, nn);
		getall_collects(nn,cb_result);
	}

	const editSearchAccount = () => {
		updStore(Acc2Collect, null);
		set_obs(gen16CodeId())
	}

	const changeViewType = (e) => {
		updStore("list_show_type_collect",e)
		updObs(gen16CodeId())
	}


	let accountD = getStore("account_ids")?.[getStore(Acc2Collect)?.account];
	let subaccountD = getStore("sub_account_ids")?.[getStore(Acc2Collect)?.subAccount];
	let idKey = getStore(Acc2Collect)?.account + "_"+ getStore(Acc2Collect)?.subAccount;

	return (
		<div className={`menu_collect ${float?"_floating":""}`} >
			<div className="_dsplFlx " style={{margin: "18px 0"}}>
					<div className=" acc_dtls">
						<div className="_dsplFlx"  >
							<div className="code" style={{color: "#646cff", cursor:"pointer"}}  >
								{accountD?.code} 
							</div>
							<div className="name">
								{accountD?. name}
							</div>
						</div>
						<div className="flexSpace"/>
						{subaccountD ?
						<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
							<div className="code" >
								{subaccountD?.subAccount} 
							</div>
							<div className="name">
								{subaccountD?. subAccountName}
							</div>
						</div>
						:null}
						{getStore(Acc2Collect)?.account && SumDocuments(getStore("collect_by_doc"),"amount")?
						
							<div className="_dsplFlx ">
							<div className="_dsplFlx " style={{padding: "35px 18px 10px "}}>
								<div className="flexSpace"/>
								<CheckBox 
									data={[
										{label: "Pendientes", id:"pending"},
										{label: "Cobrados", id:"done"}
									]}
									active={getStore("list_show_type_collect") || "pending"}
									updSelect={(e)=>changeViewType(e)}
								/>
								<div className="flexSpace"/>
							</div>
							
						</div>
						:null}
					</div>
					<div className="flexSpace"/>
					<div>
						<div className="_dsplFlx acc_dtls">
							<div className="name">
								Por Cobrar:
							</div>
							<div className="code"   style={{margin: "0px 0 0 7px", color: "#646cff"}}>
								{getStore("rec_by_acc")?.[idKey] && !isNaN(getStore("rec_by_acc")?.[idKey]*1)?` $${(getStore("rec_by_acc")?.[idKey]*1).toFixed(2)}`:null}
							</div>
						</div>
						<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
						
					</div>
				</div>
			</div>
			<WithScroll scrollhandler={handleScroll} />
		</div>

	)
}